html {
  font-family: sans-serif;
}

.main-inner {
  width: 900px;
  margin: 0 auto;
}

.btn-container {
  justify-content: space-evenly;
  display: flex;
}

.btn {
  flex-grow: 1;
}

.btn label {
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  background-color: #39f;
  border-radius: 5px;
  justify-content: center;
  padding: 5px 10px;
  display: flex;
}

.btn label:hover {
  background-color: #0080ff;
}

.preview-list {
  padding-left: 0;
}

.preview-list-item {
  border-top: 1px solid #ccc;
  align-items: center;
  height: 64px;
  list-style-type: none;
  display: flex;
}

.preview-list-item a {
  padding-left: 10px;
  line-height: 32px;
}

.preview-list-item img {
  height: 64px;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader {
  border: 5px solid #0000;
  border-bottom-color: #39f;
  border-radius: 50%;
  align-self: center;
  width: 50px;
  height: 50px;
  animation: 1s linear infinite rotation;
}
